.navbar {
    height: 12vh !important;
}


.loginbtn {
    background-color: #f8f9fa;
    font-size: 1.4rem;
    margin-left: auto;
    border: none;
}

.loginimg{
    height: 2rem;
    width: 2rem;
    margin-left: 2vw;
    margin-right: 5px;
}

.title {
    font-size: 1.9rem !important;
    font-weight: 700;
    margin-left: 3px;
    font-family: Dosis;
}

.logo>img {
    height: 8rem;
    width: 8rem;
}

.navbarcontent {
    width: 50%;
    margin-left: 34vw;
    display: flex;
    justify-content: space-between;
}

.nav-link {
    font-size: 1.2rem !important;
    font-weight: 400;
}

.nav-item>a{
    color: black;
    text-decoration: none;
    font-size: 1.1rem;
}
.nav-item>a:hover{
    color:rgb(110, 109, 109);
}