.featurescontainer{
    background-color: #169e62;
    background: radial-gradient(circle at 0.7% 1%, rgb(215, 248, 247) 0%, rgb(102, 188, 239) 100.2%);
    color: white;
    color: black;
    /* height: 50vh; */
    width: 100vw;
    padding: 3%;
    /* margin-top: 7vh; */
}
.featureheading{
    text-align: center;
    margin-top: 4vh;
    margin-bottom: 4vh;
}

.featuredesc{
    display: flex;
    text-align: center;
    font-weight: 300;
    font-size: 1.7rem;
    width: 80%;
    margin-left: 10%;
    margin-top: 2%;
}
.featuredescright{
    display: flex;
    flex-direction: column;
    text-align: center;
    font-weight: 300;
    font-size: 1.7rem;
    width: 80%;
    margin-right: 10%;
    margin-top: 2%;

}


.featureicon{
    background-color: white;
    color: #169e62;
    color: rgb(0, 119, 255);
    border-radius: 50%;
    font-size: 120px !important;
    padding: 1%;
}

.feature{
    display: flex;
    justify-content: space-evenly;
    width: 80%;
    margin: auto;
    margin-top: 7vh;
    margin-bottom: 5vh;
}

.featuredesc{
    display: flex;
    flex-direction: column;

}

.featureline{
    background-color: white;
    background-color: black;
    width: 90%;
    height: 1px;
    margin: auto;
    margin-top: 3%;
}

.featuretext{
    font-size: 1.2rem;
}