.review-container{
    /* margin-top: 7vh; */
    background-color: rgb(204, 255, 238);
    padding: 1%;
    width: 100vw;

}


#carouselExampleIndicators{

    margin-top: 70px;
    margin-bottom: 70px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.carouselouter{
font-size: 44px;
font-weight: 700;
color: rgb(80, 80, 69);
margin-bottom: 60px;
display: flex;
flex-direction: column;
align-items: center;
justify-content: space-between;
/* word-spacing: 8px; */
}

.carousel-indicators>button{
    background-color: blue!important;

}

.carousel-inner{
    background-color: white;
    width: 75vw;
    height: 45vh;
    padding: 1.3%;   
    padding-top: 0.5%;
}

.starreview{
    margin-top: 5vh;
}

.reviewsstar{
    color: gold;
    font-size:5rem !important;
}

.reviewsstarempty{
    /* border:1px solid black; */
    font-size:5rem !important;
    color: black;
}

.reviewbox{
    padding: 4%;
    padding-top: 3%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.commas>img{
    width: 30px;
    height: 30px;

}

.reviewuserinfobox{
    display: flex;
    align-items: flex-start;
}

.reviewuserimg>img{
    width: 70px;
    height: 70px;
    border-radius: 50%;
}

.reviewboxdesc{
color: rgb(82, 77, 77);
margin: 7px 0;
font-size: 1.1rem;
}

.userreviewnameandrole{
    /* background-color: aqua; */
    margin-top: 10px;
    margin-left: 15px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.reviewusername{
    font-size: 1.1rem;
    font-weight: 500;
    color: rgb(5, 17, 49);
}

.reviewuserdesc{
    font-size: 0.9rem;
    color: rgb(124, 122, 122);
    font-weight: 400;
    /* margin-left: 8px; */
}

.morereviews{
    text-align: center;
    margin: auto;
    color: rgb(9, 9, 206);
    font-weight: 500;
    margin-top: 60px;
    font-size: 24px;
    
}

.morereviews:hover{
    cursor: pointer;
    text-decoration: underline;
}

.reviewnextbtn{
    border: none;
    border-radius: 50%;
    position: absolute;
    top: 340px;
    right: 148px;
    padding: 2px;
    background-color: rgb(131, 255, 127);
    /* background-color: rgb(165, 93, 233); */
}

.reviewprevbtn{
    
    border: none;
    border-radius: 50%;
    background-color: rgb(233, 72, 23);
    /* background-color: rgb(165, 93, 233); */
    position: absolute;
    top: 340px;
    left: 148px;
}