.runningtext{
    padding: 3%;
    /* font-family: 'PT Serif'; */
    font-family: 'Times New Roman', Times, serif;
    font-style: italic;
    width: 56vw;
    height: 60vh;
}

.homepagecontainer{
    background: radial-gradient(circle at 0.7% 1%, rgb(239, 250, 250) 0%, rgb(216, 241, 255) 100.2%);
    height: 90vh;

}

#demo{
    color: rgb(9 7 126);
    font-size:2.8rem;
    line-height: 1.3;
    margin-top: 5vh;;
    margin-left: 7vw;
}

.homepagebody{
    width: 100vw;
    display: flex;
    
}

.homepagephoto{
    width: 50vw;
    height: 50vh;
    margin: auto;
    margin-top: 16vh;

     display: flex;
    align-items: center;
    justify-content: center; 
}

.homepagephoto>img{
    width: 32vw;
    height: 32vw;
    border-radius: 50%;
}
 
.homepagebutton{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 46vw;
    height: 15vh;
}

.homepagebutton>a>button{
    border-radius: 12px;
    font-size: 1.5rem;
    background-color: rgb(38, 0, 255);
    color: white;
    border: none;
    padding: 2%;
    width: 120%;
    margin-top: 32%;
}

.homepagebutton>a>button:hover{
    background-color: rgb(56, 56, 245);
}