.Appcontainer{
    background-color: rgb(201, 255, 255);
    /* height: 50vh; */
    width: 100vw;
    color: black;
    padding: 5% 1%;
}


.appheading{
    text-align: center;
}
.appheadingdesc{
    margin-top: 2vh;
    text-align: center;
    font-size: 1.3rem;
    color: grey;
}

.appfeatures{
    padding: 1%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.appfeatures>div{
    margin-top: 3vh;
}
.gettheapp{
    margin-top: 2%;
    /* background-color: rgb(185, 18, 18); */
    background-color: blue;
    padding: 8px;
    color:white;
    margin-left: 38%;
}

.gettheapp:hover{
    background-color: rgb(222, 37, 37);
    background-color: rgb(67, 67, 252);
}

.gethelp{
    margin-top: 2%;
    margin-left: 3%;
    padding: 8px;
    background-color: rgb(19, 162, 19);
    background-color: blue;
    color: white;
}

.gethelp:hover{
    
    background-color: rgb(22, 196, 22);
    background-color: rgb(67, 67, 252);
}

.applast{
    color: rgb(97, 94, 94);
    text-align: center;
    margin-top: 6%;
}